// dependancies
import React from "react";
import {
  Grid,
  Typography,
  Divider,
  Button,
  useMediaQuery,
} from "@material-ui/core";
import profile from "./kobina.jpg";
import Resume from "../Home/resume.pdf";

// icons
import { FaRegFilePdf } from "react-icons/fa";

// file imports
import AboutStyles from "./AboutStyles";

const About = () => {
  // classes and queries
  const classes = AboutStyles();
  const md = useMediaQuery("(max-width: 960px)");
  const lg = useMediaQuery("(min-width: 960px)");

  return (
	<div className={classes.background}>
    <Grid id="about" container>
        {/* Title */}
        <Grid className={classes.title} item xs={12}>
          <br />
          <Typography variant="h2">About Me</Typography>
          <Typography variant="h4">- Get To Know Me -</Typography>
          <br />
          <br />
        </Grid>
        <Grid item xs={1}></Grid>
        {md && <Grid item xs={1}></Grid>}
        <Grid item xs={10} md={5}>
          <div className={classes.picSpacing}>
            <img
              alt="Display"
              src={profile}
              className={classes.profilePic}
            ></img>
          </div>
          <br />
          <br />
          <br />
        </Grid>
        {md && <Grid item xs={1}></Grid>}

        {/* Text */}
        <Grid item xs={10} md={5}>
          <br />
          <Typography
            variant="h6"
            className={classes.subTitle}
            display="inline"
          >
          </Typography>
          <Typography
            variant="h6"
            style={{ color: "#175fca" }}
            className={classes.subTitle}
            display="inline"
          >
            {" "}
            Papa Kobina Kwegyir-Aggrey
          </Typography>
          <div style={{ paddingBottom: "2%" }} />
          <Typography variant="body1">
          I am Papa Kobina Kwegyir-Aggrey, a driven Computer Engineering student at the University of Waterloo with a rich 
          background in internships and part-time positions that have allowed me to dive deep into various technical domains.
           So far, I have completed three internships focusing on AI, machine learning, computer vision, and software engineering. 
           My professional journey has included deploying scalable machine learning models for EON Media, improving autonomous 
           vehicle navigation systems at Watonomous, and developing predictive analytics for motor lifespans at Volta Energy. 
           These experiences have sharpened my skills in Python, C/C++, MATLAB, and several developer tools like AWS, Docker, 
           and Kubernetes, enabling me to make significant contributions to cost reduction and operational efficiency in my roles. 
          I am eager to continue expanding my expertise in software 
           development and data science.{" "}
            <b> Currently, I am seeking internships for Winter 2025.</b>
          </Typography>
          <br />
          <Divider />
          <br />

          {/* Buttons */}
          {lg && (
            <a
              rel="noreferrer"
              target="_blank"
              href={Resume}
              className={classes.links}
            >
              <div>
                <Button
                  variant="contained"
                  size="large"
                  className={classes.button}
                  startIcon={<FaRegFilePdf />}
                >
                  Resume
                </Button>
              </div>
            </a>
          )}
          {md && (
            <a
              rel="noreferrer"
              target="_blank"
              href={Resume}
              className={classes.links}
            >
              <div>
                <Button
                  variant="contained"
                  size="large"
                  className={classes.buttonMobile}
                  startIcon={<FaRegFilePdf />}
                >
                  Resume
                </Button>
              </div>
            </a>
          )}
        </Grid>
        {md && <Grid item xs={1}></Grid>}

        {/* Bottom */}
        <Grid item xs={12}>
          <div style={{ paddingBottom: "7%" }} />
        </Grid>
    </Grid>
	</div>
  );
};

export default About;
