import { makeStyles } from '@material-ui/core/styles';

export const useStyles = makeStyles((theme) => ({
    title: {
        textAlign: "center",
        color: theme.palette.secondary.dark
    },
    background: {
        backgroundColor: "white"
    },
    cards: {
        paddingRight: "2%"
    },
    container: {
        backgroundColor: "#2573A6",
        width: "100%",
        height: "95%",
        backgroundSize: '200%',
        transition: '0.6s',
        backgroundImage: 'linear-gradient(45deg, #2573A6, #2573A6, #0644A3)',
        '&:hover': {
            backgroundPosition: 'right'
        },
    },
    container2: {
        backgroundColor: "#2573A6",
        marginTop: "-3%",
        width: "100%",
        height: "95%",
        backgroundSize: '200%',
        transition: '0.6s',
        backgroundImage: 'linear-gradient(45deg, #2573A6, #2573A6, #0644A3)',
        '&:hover': {
            backgroundPosition: 'right'
        },
    },
    header: {
        fontSize: "115%",
        color: "white",
        fontWeight: "501"
    },
    body: {
        color: "white",
        fontSize: "85%"
    },
    icon: {
        color: "white",
        fontSize: "200%"
    },
    tagcloudContainer: {
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        height: '100%',
        color: theme.palette.secondary.dark,
        fontSize: "1.5rem"
    },
    skillBarContainer: {
        width: '100%',
        // maxWidth: '800px',
        margin: 'auto',
        background: '#f4f4f4',
        padding: '20px'
    },
    progressBar: {
        height: '20px',
        borderRadius: '5px',
        backgroundColor: '#ddd',
        overflow: 'hidden',
        width: '100%',
        position: 'relative',
        '& .filler': {
            height: '100%',
            borderRadius: 'inherit',
            background: 'linear-gradient(45deg, #2573A6, #0644A3)',
            transition: 'width 2.0s ease-in-out',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'flex-end',
            color: '#fff',
            fontSize: '12px',
            '&:after': {
                content: 'attr(data-duration)',
                marginRight: '10px'
            }
        }
    },
    skillTitle: {
        marginBottom: '5px',
        color: '#333',
        fontSize: '16px'
    }
}));

