import './Particles.css';
import Particles from "react-tsparticles";

const Particle = () => {
  return (
    <Particles
      id="tsparticles"
      options={{
        "background": {
          "color": {
            "value": "#0d47a1"
          },
          "size": "cover"
        },
        "interactivity": {
          detectsOn: "window",
          "events": {
            "onClick": {
              "enable": true,
              "mode": "push"
            },
            "onHover": {
              "enable": true,
              "mode": "repulse",
              "parallax": {
                "force": 60
              }
            }
          },
          "modes": {
            "bubble": {
              "distance": 400,
              "duration": 2,
              "opacity": 0.8,
              "size": 40
            },
            "grab": {
              "distance": 400
            }
          }
        },
        "particles": {
          "color": {
            "value": "#FFC0CB"
          },
          "links": {
            "color": {
              "value": "#ffffff"
            },
            "distance": 150,
            "enable": true,
            "opacity": 0.4
          },
          "move": {
            "attract": {
              "rotate": {
                "x": 600,
                "y": 1200
              }
            },
            "enable": true,
            "outModes": {
              "bottom": "out",
              "left": "out",
              "right": "out",
              "top": "out"
            }
          },
          "number": {
            "density": {
              "enable": true
            },
            "value": 40
          },
          "opacity": {
            "value": 0.5,
            "animation": {
              "enable": true,
              "minimumValue": 0.1,
              "speed": 2
            }
          },
          "shape": {
            "options": {
              "polygon": {
                "nb_sides": 5
              },
              "star": {
                "nb_sides": 5
              },
              "image": {
                "src": "https://cdn.matteobruni.it/images/particles/github.svg",
                "width": 20,
                "height": 20
              },
              "images": {
                "src": "https://cdn.matteobruni.it/images/particles/github.svg",
                "width": 20,
                "height": 20
              }
            },
            "type": "star"
          },
          "size": {
            "value": 2,
            "animation": {
              "minimumValue": 10,
              "speed": 2
            }
          },
          "stroke": {
            "width": 1,
            "color": {
              "value": "#ffffff",
              "animation": {
                "enable": false,
                "speed": 2,
                "sync": true
              }
            }
          }
        }
      }
      }
    />
  );
}

export default Particle